import React, { Component } from "react";
import deleteImg from '../../images/delete.jpg';


export default class LoanConfirm extends Component {
    render(){
        return (
            <div className="loan-confirmed">
                <div className="header">
                    <a className="back_arr" href="javascript:void(0);"></a>
                    <h1></h1>
                </div>
                {/*<!-- main start -->*/}
                <div className="loan-wrap">
                    <ul className="input-list">
                        <li>
                            <div className="label"></div>
                            <div className="input-c">
                                <input type="text" value="1000" disabled="true" />
                            </div>
                        </li>
                        <li>
                            <div className="label"></div>
                            <div className="input-c">
                                <input type="text" value="7" disabled="true" />
                            </div>
                        </li>
                        <li>
                            <div className="label"></div>
                            <div className="input-c">
                                <input type="text" value="180" disabled="true" />
                            </div>
                        </li>
                        <li>
                            <div className="label"></div>
                            <div className="input-c">
                                <input type="text" value="850" disabled="true" />
                            </div>
                        </li>
                        <li>
                            <div className="label"></div>
                            <div className="input-c">
                                <input type="text" value=" (4313)" disabled="true" />
                            </div>
                        </li>
                    </ul>
                    <div className="prompt-message"><em>7</em><em>1000</em></div>
                    {/*<!-- submit-open -->*/}
                    <a href="javascript:;" className="submit-btn submit-open"></a>
                    <div className="agree-info">
                        <i className="checked"></i>
                        <span>
                          <a href=".html"></a>
                          <a href=".html"></a>
                      </span>
                    </div>
                    {/*<!--  start -->*/}
                    <div className="help-tc">
                        <div className="help-tc-inner">
                            <div className="help-text">
                                <div className="resault-msg"><i className="resault-img"></i></div>
                            </div>
                            <a href="javascript:;" className="sure-btn"></a>
                        </div>
                        <div className="overlay"></div>
                    </div>
                    {/*<!--  end -->*/}
                    {/*<!--  start -->*/}
                    <div className="password-modal">
                        <div className="password-body">
                            <div className="password-title">
                                <span></span> <em></em>
                            </div>
                            <div className="loan-money">
                                <p></p>
                                <h3> 1000.00</h3>
                            </div>
                            <div className="password-con clearfix">
                                <ul className="password-dot clearfix">
                                    <li className=""><i></i></li>
                                    <li className=""><i></i></li>
                                    <li className=""><i></i></li>
                                    <li className=""><i></i></li>
                                    <li className=""><i></i></li>
                                    <li className=""><i></i></li>
                                </ul>
                                <div className="skip-links">
                                    {/*<!---->*/}
                                    <p className="password-msg"></p>
                                    {/*<!---->*/}
                                    <a href="javascript:;" className="forget-password"></a>
                                </div>
                            </div>
                        </div>
                        <div className="keyboard">
                            <ul className="board-num clearfix">
                                <li><span>1</span></li>
                                <li><span>2</span></li>
                                <li><span>3</span></li>
                                <li><span>4</span></li>
                                <li><span>5</span></li>
                                <li><span>6</span></li>
                                <li><span>7</span></li>
                                <li><span>8</span></li>
                                <li><span>9</span></li>
                                <li><span></span></li>
                                <li><span>0</span></li>
                                <li><img src={ deleteImg } className="keyboard-delete" /></li>
                            </ul>
                        </div>
                    </div>
                    {/*<!--  end -->*/}
                </div>
            </div>
        )
    }
}


